/* 0. Reset ================================= */
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* 1. Main Layout ================================= */
* {
	box-sizing: border-box;
}

body {
	background: #D72E2D;
}

/* 2. Content ================================= */
section#nav-section {
	width: 100%;
	border-bottom: 3px white solid;
}

section#nav-section .nav-menu {
	display: flex;
	flex-direction: column;
}

@media (min-width: 992px) {
	section#nav-section .nav-menu {
		flex-direction: row;
		justify-content: space-between;
	}
}

section#nav-section .nav-menu .nav-logo {
	width: 20%;
	margin: 0 auto;
}

section#nav-section .nav-menu .nav-logo img {
	width: 100%;
}

@media (min-width: 992px) {
	section#nav-section .nav-menu .nav-logo {
		margin: 0 0;
	}
	section#nav-section .nav-menu .nav-logo img {
		width: 50%;
	}
}

@media (min-width: 1200px) {
	section#nav-section .nav-menu .nav-logo img {
		width: 35%;
	}
}

section#nav-section .nav-menu nav {
	padding: 20px;
}

section#nav-section .nav-menu nav ul {
	color: white;
	text-decoration: none;
	display: flex;
	font-family: "Open Sans", sans-serif;
	justify-content: center;
}

section#nav-section .nav-menu nav ul a {
	padding: 5px;
	margin: 10px;
	text-decoration: none;
}

section#nav-section .nav-menu nav ul a:visited {
	text-decoration: none;
	color: #FFFFFF;
}

section#titleCap .page-title {
	margin: 0 auto;
	width: 80%;
	text-align: center;
}

section#titleCap .page-title h1 {
	font-family: "Nunito Sans", sans-serif;
	font-size: 3em;
	color: #FFFFFF;
	margin: 5%;
}

section#titleCap .page-title .snippet {
	font-family: "Open Sans", sans-serif;
	font-size: 1.15em;
	line-height: 1.5em;
	color: #FFFFFF;
	padding: 10px;
}

@media (min-width: 992px) {
	section#titleCap .page-title .snippet {
		width: 50%;
		margin: 0 auto;
	}
}

section#portfolio.layout.layout-portfolio {
	padding: 20px 0;
}

section#portfolio.layout.layout-portfolio .container {
	width: 95%;
	margin: 0 auto;
	max-width: 1440px;
	position: relative;
}

section#portfolio.layout.layout-portfolio .portfolio-grid {
	padding: 25px 0;
	position: relative;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

section#portfolio.layout.layout-portfolio .portfolio-grid .single-project {
	width: 100%;
	text-align: center;
	margin: 15px 0;
	position: relative;
	padding: 0 15px;
	height: 275px;
	background-size: cover;
	background-image: url("/dist/img/straws.jpg");
	transition: all 0.2s ease-out;
}

@media (min-width: 768px) {
	section#portfolio.layout.layout-portfolio .portfolio-grid .single-project {
		width: 49%;
		height: 250px;
	}
}

@media (min-width: 992px) {
	section#portfolio.layout.layout-portfolio .portfolio-grid .single-project {
		width: 29%;
		height: 200px;
	}
}

@media (min-width: 1200px) {
	section#portfolio.layout.layout-portfolio .portfolio-grid .single-project {
		width: 24%;
		height: 200px;
	}
}

section#portfolio.layout.layout-portfolio .inner .img-overlay {
	width: 100%;
	height: 100%;
	background-color: rgba(22, 59, 91, 0.5);
	position: absolute;
	top: 0;
	left: 0;
}

section#portfolio.layout.layout-portfolio .inner .img-overlay .text-container {
	position: absolute;
	top: 50%;
	left: 0;
	transform: translateY(-50%);
	width: 100%;
	padding: 0 20px;
	color: #FFFFFF;
	font-family: "Open Sans", sans-serif;
	font-size: 1.5em;
}

section#portfolio.layout.layout-portfolio .inner .img-overlay .text-container .project-subtitle {
	display: block;
	color: #ecde4b;
	font-family: "Nunito Sans", sans-serif;
}

@media (min-width: 1200px) {
	section#portfolio.layout.layout-portfolio .inner .img-overlay .text-container .project-subtitle {
		display: none;
		margin-top: 10px;
	}
}

@media (min-width: 1200px) {
	section#portfolio.layout.layout-portfolio .inner .img-overlay:hover .project-subtitle {
		display: block;
		animation: slideDown .3s ease-in-out forwards;
		transition: all;
	}
}

